// export default Navbar1;


import React, { useContext} from 'react';
import { NavLink } from 'react-router-dom';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { UserContext} from '../App';
import logos from './cartlogo1.jpeg';


import "./Navbar1.css";

const Navbar1 = () => {
    const { state, dispatch } = useContext(UserContext);
    const RenderMenu = () => {
      if(state){
        return (
          <>
           
      <ul className="navbar-nav me-auto">
        <li className="nav-item">
        <NavLink className="nav-link" to="/">Home</NavLink>
          
        </li>
        <li className="nav-item">
         
          <NavLink className="nav-link" to="/about">About</NavLink>          
        </li>       
        <li className="nav-item">
          
          <NavLink className="nav-link" to="/contact">Contact</NavLink>
        </li>
          {/* <li>
        <NavDropdown id="collasible-nav-dropdown" className="md-6" title="More"  menuVariant="light">            
              <NavDropdown.Item href="javascript:void(0)" >
                <NavLink className="nav-link" to="/stopwatch">
                  Stopwatch
                </NavLink>
                </NavDropdown.Item>
                <NavDropdown.Item href="javascript:void(0)" >
                <NavLink className="nav-link" to="/helloworld">
                  Helloworld
                </NavLink>
                </NavDropdown.Item>
                <NavDropdown.Item href="javascript:void(0)" >
                <NavLink className="nav-link" to="/clock">
                  Clock
                </NavLink>
                </NavDropdown.Item>
      
        </NavDropdown>

        </li> */}
           
      </ul>

      <NavLink className="navbar-brand fw-bolder fs-4 mx-auto" to="/">Web Cart</NavLink>

<ul className="navbar-nav me-auto">
        <li className="nav-item">
        <NavLink className="nav-link" to="/Login">Login</NavLink>
          
          
        </li>
        <li className="nav-item">
        <NavLink className="nav-link" to="/Signup">Register</NavLink>         
        </li>

        </ul>
          </>
        )
    } else {
      return(
        <>
         
<ul className="navbar-nav me-auto">
        <li className="nav-item">
        <NavLink className="nav-link" to="/">Home</NavLink>
          
        </li>
        <li className="nav-item">
         
          <NavLink className="nav-link" to="/about">About</NavLink>          
        </li>       
        <li className="nav-item">
          
          <NavLink className="nav-link" to="/contact">Contact</NavLink>
        </li>

        <li className="nav-item">
          
          <NavLink className="nav-link" to="/Menu">Menu</NavLink>
        </li>

        <li className="nav-item dropdown">
          
        <NavDropdown id="collasible-nav-dropdown" className="md-6" title="More"  menuVariant="light">            
              <NavDropdown.Item href="javascript:void(0)" >
                <NavLink className="nav-link" to="/stopwatch">
                  Stopwatch
                </NavLink>
                </NavDropdown.Item>
                <NavDropdown.Item href="javascript:void(0)" >
                <NavLink className="nav-link" to="/helloworld">
                  Helloworld
                </NavLink>
                </NavDropdown.Item>
                <NavDropdown.Item href="javascript:void(0)" >
                <NavLink className="nav-link" to="/clock">
                  Clock
                </NavLink>
                </NavDropdown.Item>
      
        </NavDropdown>
           
       </li>

      </ul>

      <NavLink className="navbar-brand fw-bolder fs-4 mx-auto" to="/">Web Cart</NavLink>

<ul className="navbar-nav me-auto">
        
        <li className="nav-item">
        <NavLink className="nav-link" to="/Logout"><button className="btn btn-outline-secondary" type="submit">Logout</button></NavLink>         
        </li>

        </ul>
       
        </>
      )
    }
  }
  return(
    <>
           

<nav className="navbar navbar-expand-lg shadow navcolor">

  <div className="container-fluid">

  <a class="navbar-brand" href="#">
    <img src={logos} width="40" height="40" class="d-inline-block align-top" alt="image"></img>
  </a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <RenderMenu />
    </div>
    </div>
    </nav>
      </>
  )
}
export default Navbar1;





